<template>
  <div>
    <b-table
      ref="selectableTable"
      :busy="isBusy"
      :fields="fieldss"
      :items="subsidyAssesesmentData"
      :borderless="false"
      :outlined="false"
      :bordered="false"
      :striped="false"
      :select-mode="selectModeTable"
      small
      fixed
      sticky-header
      responsive="sm"
      head-variant="light"
    >
      <template v-slot:cell(SubsidyStatus)="item">{{ item.value }}</template>
      <template v-slot:cell(assesmentdate)="item">{{ item.value }}</template>
      <template v-slot:cell(assesmentRequestdate)="item">{{
        item.value
      }}</template>
      <template v-slot:cell(remarks)="item">{{ item.value }}</template>
      <template v-slot:cell(SubsidyPersonIncharge)="item">{{
        item.value
      }}</template>
      <template v-slot:cell(Icon)="item">
        <b-button @click="subsidyDetail(item)" variant="primary">{{
          $t("DetailsButton")
        }}</b-button>
      </template>
      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
    </b-table>
    <div class="text-center text-danger mb-2 mt-2">{{ errorMessage }}</div>
    <b-row>
      <b-col cols="12" xl="9" class="mt-4 ml-5">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          align="center"
          size="sm"
        >
          <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
          <template v-slot:next-text>{{ $t("NEXT") }}</template>
          <template v-slot:page="{ page, active }">
            <strong v-if="active">{{ page }}</strong>
            <i v-else>{{ page }}</i>
          </template>
        </b-pagination>
      </b-col>
      <b-col cols="12" xl="2" class="float-right mt-4">
        <div class="countList">
          <label>{{ $t("MEANS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-link :key="index" v-if="limit == page" href="javascript:;">
              {{ page }}
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
            <b-link
              :key="index"
              @click="perPageChange(page)"
              v-else
              href="javascript:;"
            >
              <u>{{ page }}</u>
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
          </template>
        </div>
      </b-col> </b-row
    ><br />
  </div>
</template>
<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  data() {
    return {
      subsidyAssesesmentData: [],
      selectModeTable: "single",
      selected: [],
      isBusy: false,
      total: 0,
      limit: 50,
      start: 0,
      currentPage: 1,
      pageOptions: [50, 100, 200],
      errorMessage: "",
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      client_details: "getClientData",
    }),
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    items() {
      return this.subsidyAssesesmentData;
    },
    rows() {
      return this.subsidyAssesesmentData.length;
    },
    lists() {
      const items = this.subsidyAssesesmentData;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
    fieldss() {
      return [
        {
          key: "SubsidyStatus",
          label: this.$t("SITUATION"),
          class: "text-center normal-width",
        },
        {
          key: "assesmentdate",
          label: this.$t("ASSESSMENTREQUESTDATE"),
          class: "text-center normal-width",
        },
        {
          key: "assesmentRequestdate",
          label: this.$t("APPLICATIONDATE2"),
          class: "text-center normal-width",
        },
        {
          key: "remarks",
          label: this.$t("REMARKS"),
          class: "text-left normal-width",
        },

        {
          key: "SubsidyPersonIncharge",
          label: this.$t("CONTACT"),
          class: "text-center normal-width",
        },
        {
          key: "Icon",
          label: "",
          class: "text-center normal-width",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  mounted() {
    this.subSidyInfo();
  },
  created() {},
  methods: {
    /**
     * @description function to get the row count of list
     * @name getRowCount
     */    
    getRowCount: function () {
      return this.items.length;
    },
    /**
     * @description function to go get previous list
     * @name pageChange
     */    
    pageChange(page) {
      this.start = page - 1;
      this.subSidyInfo();
    },
        /**
     * @description function to get next  page list
     * @name perPageChange
     */
    perPageChange(page) {
      this.limit = page;
      this.subSidyInfo();
    },
    /**
     * @description function to call the subsidyAssesmentDetails page
     * @name subsidyDetail
     */    
    subsidyDetail(item) {
      console.log(item);
      this.$store.dispatch("setAssesmentNo", {
        assesmentno: item.item.assessment_no,
      });
      this.$router.push({ name: "client_mgmt_subsidy_assessment_detail" });
    },
    onRowSelected(items) {
      this.$emit("send_selected_data", items);
      this.selected = items;
    },
    /**
     * @description function to fetch the subsidyDetails
     * @name subsidyInfo
     */    
    async subSidyInfo() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADWITHJOINTSUBSIDYASSESSMENT,
        {
          limit: this.limit,
          offset: this.start * 10,
          paginate: true,
          fieldlist: ["t_subsidy_assessment.*", "m_person.person_name"],
          conditions: [
            {
              key: "t_subsidy_assessment.client_no",
              value: this.client_details.client_no,
            },
          ],
          joins: [
            {
              table: "m_person",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_person.person_id",
                  join_operator: "=",
                  join_with: "t_subsidy_assessment.assessment_person_id",
                },
              ],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data.data;
        let sortData = this._.sortBy(responseData, "updated_at").reverse();
        console.log(responseData);
        this.total = 0;
        if (sortData.length > 0) {
          let subsidyAssesesmentInfo = [];
          sortData.forEach((item) => {
            let subsidyType;
            //subsidyType  name is given based on the assesment_status_no
            if (item.assessment_sts_kbn == "01") {
              subsidyType = this.$t("SUBSIDY.ASSESSMENTREQUEST");
            } else if (item.assessment_sts_kbn == "02") {
              subsidyType = this.$t("SUBSIDY.UNDERASSESSMENT");
            } else if (item.assessment_sts_kbn == "03") {
              subsidyType = this.$t("SUBSIDY.APPLYING");
            } else if (item.assessment_sts_kbn == "04") {
              subsidyType = this.$t("SUBSIDY.APPLIED");
            } else {
              subsidyType = "";
            }
            let upDateAt;
            if (item.updated_at == null) {
              upDateAt = item.update_dt;
            } else {
              upDateAt = item.updated_at;
            }
            let assesesmentDate;
            let assesmentRequestdate;
            if (item.assessment_dt != null) {
              //Dt is assessment fetched from db
              let Dt = new Date(item.assessment_dt);
              assesesmentDate =
                Dt.getFullYear() +
                "/" +
                this.getYearMonth(parseInt(Dt.getMonth() + 1).toString()) +
                "/" +
                this.getYearMonth(Dt.getDate().toString());
            }
            if (item.request_dt != null) {
              let Dt2 = new Date(item.request_dt);
              assesmentRequestdate =
                Dt2.getFullYear() +
                "/" +
                this.getYearMonth(parseInt(Dt2.getMonth() + 1).toString()) +
                "/" +
                this.getYearMonth(Dt2.getDate().toString());
            } else {
              assesmentRequestdate = "-";
            }
            let assesesmentData = {
              SubsidyStatus: subsidyType,
              assesmentdate: assesesmentDate,
              assesmentRequestdate: assesmentRequestdate,
              remarks: item.remarks,
              SubsidyPersonIncharge: item.person_name,
              assessment_no: item.assessment_no,
              updated_at: upDateAt,
            };
            subsidyAssesesmentInfo.push(assesesmentData);
          });
          this.subsidyAssesesmentData = this._.sortBy(subsidyAssesesmentInfo, "updated_at").reverse();
          this.isBusy = false;
        } else {
          this.isBusy = false;
          this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
        }
      } else {
        this.isBusy = false;
        this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
      }
    },
    /**
     * @description function to get formated Date As (MM) 
     * @name getYearMonth
     */
    getYearMonth(item) {
      let data = "";
      if (item.slice(0, 2).length == 1) {
        data += "0" + item.slice(0, 2);
      } else {
        data += item.slice(0, 2);
      }
      return data;
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
$unread_color: #afabab;
$icon_background: red;
.ContractInfo {
  font-size: revert;
  margin-left: 10px;
}
.b-table-sticky-header {
  border: 1px solid #c8ced3;
}
</style>
