<template>
  <div>
    <b-row class="d-flex align-items-center p-4">
      <b-col cols="12" xl="12">
        <b-form @submit.prevent="submit">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="3">
              <b-button @click="redirect" variant="primary">{{
                $t("ADDCUSTOMERSERVICE")
              }}</b-button>
            </b-col>

            <b-col xl="auto">
              <b-row>
                <b-col>
                  <b-form-radio-group
                    id="radio-group-1"
                    v-model="selectRows"
                    :options="rowsOptions"
                    @change="UpdateMessages"
                    class="radioControl"
                    size="sm"
                  ></b-form-radio-group>
                </b-col>
              </b-row>
            </b-col>

            <b-col cols="12" xl="1"></b-col>
            <b-col cols="12" xl="3">
              {{ $t("DISPLAYORDER") }}
              <b-link
                @click="changeOrder('DESC')"
                href="javascript:;"
                class="card-link link ml-2"
              >
                <u>{{ $t("SENDDATEASC") }}</u>
              </b-link>
              <b-link
                @click="changeOrder('ASC')"
                href="javascript:;"
                class="card-link link ml-2"
              >
                <u>{{ $t("SENDDATEDESC") }}</u>
              </b-link>
            </b-col>
          </b-row>
          <br />
          <div v-if="pageloading" class="text-center">
            <i class="fa fa-spinner fa-spin fa-2x" />
          </div>
          <div v-else>
            <MessageComponent :messageList="MessageList"></MessageComponent>
          </div>
        </b-form>
      </b-col>
    </b-row>
  </div>
</template>
<script>
import MessageComponent from "@/components/MessageComponent";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import i18n from "@/plugins/i18n";
import { Config } from "@/shared/config";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "ClientManagement",
  components: { MessageComponent },
  data() {
    return {
      tabIndex: 10,
      MessageList: [],
      selectRows: "",
      order_by: "desc",
      appUrl: Config.apiBaseUrl,
      pageloading: false,
    };
  },
  computed: {
    ...mapGetters({
      client_details: "getClientData",
    }),
    rowsOptions() {
      return [
        { text: i18n.t("SHOWALL"), value: "" },
        { text: "30 " + i18n.t("COUNTS"), value: "30" },
        { text: "100 " + i18n.t("COUNTS"), value: "100" },
        { text: "1000 " + i18n.t("COUNTS"), value: "1000" },
      ];
    },
  },
  mounted() {
    this.getMessageInfo();
  },
  methods: {
    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ["bg-white", "text-black"];
      } else {
        return ["bg-primary", "text-black"];
      }
    },
    linkClassMl4(idx) {
      if (this.tabIndex === idx) {
        return ["bg-white", "text-black", "ml-4"];
      } else {
        return ["bg-primary", "text-black", "ml-4"];
      }
    },
    redirect() {
      this.$store.dispatch("setQuotationNo", {
        Quotation_no: "",
        Proposal_no: "",
        Supplier: "",
        supplierNo : ""
      });
      this.$store.dispatch("setAssesmentNo", {
        assesmentno: "",
      });
      this.$router.push({ name: "client_mgmt_client_transaction_add" });
    },
    changeOrder(order) {
      this.order_by = order;
      this.getMessageInfo();
    },
    UpdateMessages() {
      setTimeout(() => {
        this.getMessageInfo();
      }, 0);
    },
    async getMessageInfo() {
      let Messages = [];
      this.pageloading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADCLIENTCOMMUNICATION,
        {
          client_no: this.client_details.client_no,
          limit: this.selectRows,
          order_by: this.order_by,
        }
      );
      this.pageloading = false;
      if (data.success) {
        let responseData = data.data.communicationListObject;
        responseData.forEach((item) => {
          let MessageDate = new Date(item.message_dt);
          MessageDate = new Date(
            MessageDate - MessageDate.getTimezoneOffset() * 1000 * 60
          );
          let MessageMonth = MessageDate.getMonth() + 1;
          let Day = MessageDate.getDate();
          let Time =
            ("0" + MessageDate.getHours()).slice(-2) +
            ":" +
            ("0" + MessageDate.getMinutes()).slice(-2);

          let iconPath = "";
          if (
            item.message_sender_icon_path &&
            item.message_sender_icon_path != "undefined" &&
            item.message_sender_icon_path != "null"
          ) {
            iconPath = this.appUrl + item.message_sender_icon_path;
          }

          let messageData = {
            messageNo: item.message_no,
            attached_file_data: item.attachments,
            senderBelongs: item.message_sender_belongs,
            personName: item.message_sender_person_name,
            messageTitle: item.message_title,
            morningMessage: item.message_text,
            message_sender_account_type: item.message_sender_account_type,
            IconPath: iconPath,
            AttchMentFlag: item.attached_file_flg,
            SupportType: item.client_support_type,
            Month: MessageMonth,
            Day: Day,
            Time: Time,
          };
          Messages.push(messageData);
        });
        this.MessageList = Messages;
      }
    },
  },
};
</script>
<style>
.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
}
.no-gutters {
  margin-right: -31px !important;
}
.file-btn-margin {
  margin-left: 10px;
  margin-top: 0.2rem;
}
#fileSelect label {
  margin-bottom: 0px;
}
.hrType {
  border-width: 10;
  border-top: 1px dotted gray;
  border-bottom: 1px dotted gray;
}
</style>
