<template>
  <div>
    <b-row v-if="loading" class="text-center">
      <b-col cols="12">
        <b-spinner class="ml-auto"></b-spinner>
      </b-col>
    </b-row>
    <div v-else>
      <b-row class="my-2">
        <b-col cols="12" xl="1"></b-col>
        <b-col cols="12" xl="3" class="label-custom-ce">
          <div class="font-weight-bold">{{ $t("CURRENTPOINTSTAGE") }}:</div>
        </b-col>
        <b-col cols="12" xl="6">
          <div v-if="clientInfo.judg_company">
            {{
              $t("DASHBOARD.OVERALL_JUDGEMENTS")[clientInfo.judg_company - 1]
                .LABEL
            }}
          </div>
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col xl="1"></b-col>
        <b-col xl="3" class="label-custom-ce">
          <div class="font-weight-bold">{{ $t("CURRENTEXCELLENTPOINT") }}:</div>
        </b-col>
        <b-col xl="6">
          <div v-if="clientInfo.clientPointBalanceObject">
            {{ clientInfo.owned_point }}P ( {{ $t("INSIDE") }}
            {{ clientInfo.clientPointBalanceObject.sum }} {{ $t("BUT") }}
            {{ getDate(clientInfo.clientPointBalanceObject.expiration_date) }}
            {{ $t("EXPIRATIONDATECLIENTEXCELPT") }} )
          </div>
        </b-col>
      </b-row>
      <b-row class="mt-2 mb-2">
        <b-col xl="1"></b-col>
        <b-col xl="3" class="lable-custom-ce">
          <div class="font-weight-bold">{{ $t("POINTSEARNEDTHISMONTH") }}:</div>
        </b-col>
        <b-col xl="6">
          <div>{{ clientInfo.current_month_point }}P</div>
        </b-col>
      </b-row>
      <div id="settingsCard" v-if="tableData.length > 0" class="p-2">
        <b-table
          ref="selectableTable"
          :fields="fieldss"
          :items="tableData"
          :bordered="false"
          :striped="false"
          :borderless="false"
          :busy="isBusy"
          :per-page="limit"
          :total="total"
          :startPage="start"
          @change-page="pageChange"
          :outlined="false"
          :responsive="true"
          :tbody-tr-class="rowClass"
          head-variant="light"
          fixed
          sticky-header
        >
          <template v-slot:cell(work_dt)="item">{{
            getDate(item.value)
          }}</template>
          <template v-slot:cell(history_kbn)="item">
            <span
              :class="
                item.value == '02' || item.value == '03' ? 'text-danger' : ''
              "
            >
              {{ getHistoryClassification(item.value) }}
            </span>
          </template>
          <template v-slot:cell(point)="item">
            <span
              v-if="
                item.item.history_kbn == '02' || item.item.history_kbn == '03'
              "
              >-</span
            >
            {{ item.value }}P
          </template>
          <template v-slot:cell(stage_type)="item">
            {{ getCompanyStandard(item.value) }}
          </template>
          <template v-slot:cell(PersonIncharge)="item">
            <span
              :class="
                item.value == '02' || item.value == '03' ? 'text-danger' : ''
              "
            >
              {{ item.value }}
            </span>
          </template>
          <template v-slot:cell(CreateDate)="item">{{ item.value }}</template>
          <template v-slot:cell(UpdateDate)="item"
            ><span>
              {{ item.value }}
            </span>
          </template>
          <template v-slot:cell(Icon)>
            <b-button variant="primary" class="Detailsbtn">{{
              $t("DetailsButton")
            }}</b-button>
          </template>
          <template v-slot:table-busy>
            <div class="text-center text-danger my-2">
              <b-spinner class="align-middle"></b-spinner>
            </div>
          </template>
        </b-table>
      </div>
      <b-row>
        <b-col cols="12" xl="4"></b-col>
        <b-col cols="12" xl="5">
          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="limit"
            class="paginateProcess"
            size="sm"
          >
            <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
            <template v-slot:next-text>{{ $t("NEXT") }}</template>
            <template v-slot:page="{ page, active }">
              <strong v-if="active">{{ page }}</strong>
              <i v-else>{{ page }}</i>
            </template>
          </b-pagination>
        </b-col>
        <b-col cols="12" xl="3">
          <div class="countList">
            <label>{{ $t("MEANS") }}</label
            >&nbsp;&nbsp;
            <template v-for="(page, index) in pageOptions">
              <b-link :key="index" v-if="limit == page" href="javascript:;">
                {{ page }}
                <span v-if="index != pageOptions.length - 1"
                  >&nbsp;|&nbsp;</span
                >
              </b-link>
              <b-link
                :key="index"
                @click="perPageChange(page)"
                v-else
                href="javascript:;"
              >
                <u>{{ page }}</u>
                <span v-if="index != pageOptions.length - 1"
                  >&nbsp;|&nbsp;</span
                >
              </b-link>
            </template>
          </div>
        </b-col>
      </b-row>
    </div>
    <br />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";

export default {
  name: "SupplierList",
  components: {},
  data() {
    return {
      pagination: {},
      loading: false,
      clientInfo: {},
      companyStandard: "",
      expenseItemOptions: [],
      start: 0,
      total: 0,
      checkedItems: [],
      selectedExpenseItem: {
        accounts_name_cd: "",
        expense_item_cd: "",
      },
      options: [
        { value: "01", text: this.$t("UNPROCESSED") },
        { value: "02", text: this.$t("PROCESSING") },
        { value: "03", text: this.$t("PROCESSED") },
      ],
      limit: 50,
      currentPage: 1,
      perPage: 3,
      pageOptions: [50, 100, 200],
      userData: [],
      tableData: [],
      isBusy: false,
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      client_details: "getClientData",
    }),
    totalRows() {
      return 10;
    },
    fieldss() {
      return [
        {
          key: "work_dt",
          label: this.$t("TASK.PROCESSINGDATETIME"),
          class: "text-center  date-normal-width",
        },
        {
          key: "history_kbn",
          label: this.$t("HISTORYCLASSIFICATION"),
          class: "text-center  normal-width",
        },
        {
          key: "point",
          label: this.$t("POINTS"),
          class: "text-center  normal-width",
        },
        {
          key: "stage_type",
          label: this.$t("STAGE"),
          class: "text-left  normal-width",
        },
        {
          key: "history_content",
          label: this.$t("POINTSEARNEDCONSUMED"),
          class: "text-left  title-normal-width",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  mounted() {
    this.getReadClientPointInfo();
    this.getExpenseItemList();
  },

  methods: {
    /**
     * Add colour for the entire row according to the condition
     */
    rowClass(item, type) {
      if (item && type === "row") {
        if (item.history_kbn == "02" || item.history_kbn == "03") {
          return "text-danger";
        } else {
          return "";
        }
      } else {
        return null;
      }
    },
    /**
     * format the date string
     */
    getDate(date) {
      if (!date) {
        return "";
      }
      return (
        date.slice(0, 4) +
        "/" +
        date.slice(5, 7) +
        "/" +
        date.slice(8, 10) +
        "  " +
        date.slice(11, 16)
      );
    },
    getHistoryClassification(val) {
      if (val == "01") return this.$t("OBTAIN");
      if (val == "02") return this.$t("CONSUMPTION");
      if (val == "03") return this.$t("EXPIRED");
    },
    getCompanyStandard(val) {
      if (val == "001") return this.$t("EXCELLENT");
      if (val == "002") return this.$t("GREAT");
      if (val == "003") return this.$t("GOOD");
      if (val == "004") return this.$t("STANDARD");
    },
    getCheckBoxControl() {
      return true;
    },

    toggleSelectAll(item) {
      item.item.checkState = !item.item.checkState;
      this.checkedItems = this.userData.filter((checkedItem) => {
        if (checkedItem.checkState == true) {
          return checkedItem;
        }
      });
    },

    async getReadClientPointInfo() {
      this.loading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADCLIENTPOINTINFO,
        {
          client_no: this.client_details.client_no,
        }
      );
      if (data.success) {
        this.clientInfo.judg_company = data.data.clientJudgInfoTObject
          ? parseInt(data.data.clientJudgInfoTObject.judg_company)
          : "";
        this.clientInfo.owned_point = data.data.clientPointInfoObject
          ? data.data.clientPointInfoObject.owned_point
          : "";
        this.clientInfo.current_month_point = data.data.clientPointInfoObject
          ? data.data.clientPointInfoObject.current_month_point
          : "";
        this.clientInfo.clientPointBalanceObject = data.data
          .clientPointBalanceObject
          ? data.data.clientPointBalanceObject
          : "";

        this.loading = false;
      }
      this.loading = false;
    },
    async getExpenseItemList() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADCLIENTEXCELLENTPOINT,
        {
          client_no: this.client_details.client_no,
          limit: this.limit,
          paginate: true,
          page: this.currentPage,
        }
      );
      this.total = 0;
      if (data.success) {
        this.total = data.data.total;
        let responseData = data.data.data;
        this.tableData = responseData;
        this.isBusy = false;
      } else {
        this.tableData = [];
        this.isBusy = false;
      }
    },
    getRowCount() {
      return this.items.length;
    },
    pageChange(page) {
      this.start = page - 1;
      this.getExpenseItemList();
    },
    perPageChange(page) {
      this.limit = page;
      this.getExpenseItemList();
    },
    onChange() {
      this.getExpenseItemList();
    },
  },
};
</script>
<style scoped lang="scss">
.label-custom-ce {
  width: 40%;
}
.searchSelect1 {
  position: relative !important;
  flex: 1 1 auto !important;
  width: 1% !important;
  margin-bottom: 0 !important;
  height: 30px !important;
}
.input-group-text {
  background-color: none;
  border: none;
}

.filterCard {
  border: 1px dashed;
}

.normal-width {
  width: 80px !important;
}
.title-normal-width {
  width: 110px !important;
}
.date-normal-width {
  width: 110px !important;
}
.check-normal-width {
  width: 80px !important;
  padding-top: 15px !important;
}
.custom-select {
  height: 30px;
  border-color: rgb(163, 163, 163);
}
.paginateProcess {
  padding-top: 10px;
}
.countList {
  padding-top: 10px;
}
.text-sm {
  font-size: 10px;
}
</style>
<style lang="scss">
.normal-width {
  width: 80px !important;
}
.title-normal-width {
  width: 110px !important;
}
.date-normal-width {
  width: 110px !important;
}
.check-normal-width {
  width: 80px !important;
  padding-top: 15px !important;
}
</style>