<template>
  <div class="p-1 pt-4">
    <b-form @submit.prevent="submit">
      <b-row>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-1">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("COMPANYNAMEORNAME")
            }}</label>
          </b-col>
          <b-col cols="12" class="mt-3">
            <b-form-input
              v-model.trim="$v.basicSettingInfo.company_name.$model"
              maxlength="100"
              class="textFieldClass"
            ></b-form-input>
            <show-errors
              :control="$v.basicSettingInfo.company_name"
              :field="$t('COMPANYNAMEORNAME')"
            />
          </b-col>
        </b-col>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-1">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("PERSONINCHARGE")
            }}</label>
          </b-col>
          <b-col cols="12" class="mt-3">
            <b-form-input
              v-model="$v.basicSettingInfo.person_in_charge.$model"
              maxlength="50"
              class="textFieldClass"
            ></b-form-input>
            <show-errors
              :control="$v.basicSettingInfo.person_in_charge"
              :field="$t('PERSONINCHARGE')"
            />
          </b-col>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-1">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("PHONENO") }}</label>
          </b-col>
          <b-col cols="12" class="mt-3">
            <b-form-input
              v-model.trim="$v.basicSettingInfo.phoneNo.$model"
              maxlength="11"
              class="textFieldClass"
              onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
            ></b-form-input>
            <show-errors
              :control="$v.basicSettingInfo.phoneNo"
              :field="$t('PHONENO')"
            />
          </b-col>
        </b-col>
        <b-col cols="12" xl="6">
          <b-col cols="12" class="mt-1">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("MAILADDRESS") }}</label>
          </b-col>
          <b-col cols="12" class="mt-3">
            <b-form-input
              v-model.trim="$v.basicSettingInfo.email.$model"
              maxlength="254"
              class="textFieldClass"
            ></b-form-input>
            <show-errors
              :control="$v.basicSettingInfo.email"
              :field="$t('MAILADDRESS')"
            />
          </b-col>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12">
          <b-col cols="12" class="mt-1">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >&nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("Headquarterslocation")
            }}</label>
          </b-col>
          <b-col id="options" cols="12" xl="2" class="mt-3">
            <b-form-select
              v-model="selectedoffice"
              :plain="true"
              :options="officeOptions"
              value-field="item"
              text-field="name"
              disabled-field="notEnabled"
              class="optionClass"
            ></b-form-select>
          </b-col>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12">
          <b-col cols="12">
            <b-form-input
              v-model.trim="$v.basicSettingInfo.officeAddress.$model"
              :placeholder="$t('HEADQUARTERSCITY')"              
              maxlength="300"
              class="textFieldClass"
            ></b-form-input>
            <show-errors
              :control="$v.basicSettingInfo.officeAddress"
              :field="$t('HeadquartersAddress')"
            />
          </b-col>
        </b-col>
      </b-row>

      <br />
      <br />
      <b-row>
        <b-col cols="12" xl="12" class="text-center">
          <b-button
            @click="updateSettingsInfo"
            variant="primary"
            class="px-4 align-content-center marginbtn"
            >{{ $t("UPDATE") }}</b-button
          >
        </b-col>
      </b-row>
      <br />
    </b-form>
  </div>
</template>
<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { mapGetters } from "vuex";
import {
  required,
  email,
  numeric,
  minLength,
} from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";

export default {
  name: "Index",
  components: { ShowErrors },
  data() {
    return {
      selectedoffice: "1",
      officeOptions: [],
      currentMonth: new Date().getMonth() + 1,
      basicSettingInfo: {
        company_name: "",
        person_in_charge: "",
        email: "",
        officeAddress: "",
        phoneNo: "",
      },
      ClientDataArray: [],
    };
  },
  computed: {
    ...mapGetters({
      client_details: "getClientData",
    }),
  },
  validations: {
    basicSettingInfo: {
      company_name: { required },
      person_in_charge: { required },
      email: { required, email },
      officeAddress: { required },
      phoneNo: {
        required,
        phoneNumber: numeric,
        phoneMinLength: minLength(10),
      },
    },
  },
  mounted() {
    this.getClientData();
    this.prefectureData();
  },
  created() {},
  methods: {
    /**
     * @description function to fetch office Address
     * @name prefectureData
     */
    async prefectureData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READMGENERALPURPOSE,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "key",
              value: "PREF",
            },
          ],
        }
      );
      let responseData = data.data;
      let codevalue = [];
      let value = [];
      let optiondata = [];
      responseData.forEach((item) => {
        codevalue.push(parseInt(item.code));
        value = item.value_string_small;
        let i = 1;
        let insert = 0;
        if (codevalue[insert] == i) {
          optiondata.push(value);
          i = i + 1;
        }
      });

      for (let index = 0; index < optiondata.length; index++) {
        this.officeOptions.push({
          item: codevalue[index],
          name: optiondata[index],
        });
      }
    },
    /**
     * @description function fetch clientDetails Based On ClientNo
     * @name getClientData
     */
    async getClientData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTBASEINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: this.client_details.client_no,
            },
          ],
        }
      );
      if (data.success) {
        this.ClientDataArray = data.data;
        if (this.ClientDataArray.length > 0) {
          let responseData = data.data;
          this.basicSettingInfo.company_name = responseData[0].client_name;
          this.basicSettingInfo.person_in_charge =
            responseData[0].client_person_name;
          this.basicSettingInfo.phoneNo = responseData[0].client_tel;
          this.basicSettingInfo.email = responseData[0].client_mail;
          this.selectedoffice = responseData[0].client_main_office_kbn;
          this.basicSettingInfo.officeAddress =
            responseData[0].client_main_office_addr;
          this.cntBaseInfoDate = responseData[0].create_dt;
        }
      }
    },
    /**
     * @description function to Check ClientDetails Alredy Exists Or Not
     * @name updateSettingsInfo
     */
    updateSettingsInfo() {
      this.$v.basicSettingInfo.$touch();
      if (!this.$v.basicSettingInfo.$invalid) {
        let self = this;
        this.$swal({
          text: this.$t("InsertMessage"),
          showCancelButton: true,
          confirmButtonText: "OK",
          cancelButtonText: this.$t("CANCEL"),
          cancelButtonColor: "#062854",
          cancelButtonClass: "btn-primary",
        }).then(async function (isconfirm) {
          if (!isconfirm.value == true) {
            return;
          } else {
            if (self.ClientDataArray.length > 0) {
              self.callUpdateAPI();
            } else {
              self.callCreateAPI();
            }
          }
        });
      }
    },
    /**
     * @description function to update clientDetails Based on clientNo
     * @name callUpdateAPI
     */
    async callUpdateAPI() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATEMCLIENTBASEINFO,
        {
          data: [
            {
              client_no: this.client_details.client_no,
              client_account_no: this.client_details.client_no,
              client_mail: this.basicSettingInfo.email,
              client_name: this.basicSettingInfo.company_name,
              client_person_name: this.basicSettingInfo.person_in_charge,
              client_tel: this.basicSettingInfo.phoneNo,
              client_main_office_kbn: this.selectedoffice,
              del_flg: false,
              create_id: this.client_details.client_no,
              create_dt: this.cntBaseInfoDate,
              update_id: this.client_details.client_no,
              update_dt:
                new Date().getFullYear() +
                "-" +
                this.currentMonth +
                "-" +
                new Date().getDate() +
                " " +
                new Date().getHours() +
                ":" +
                new Date().getMinutes() +
                ":" +
                new Date().getSeconds(),
              created_at: this.cntBaseInfoDate,
              updated_at:
                new Date().getFullYear() +
                "-" +
                this.currentMonth +
                "-" +
                new Date().getDate() +
                " " +
                new Date().getHours() +
                ":" +
                new Date().getMinutes() +
                ":" +
                new Date().getSeconds(),
              client_main_office_addr: this.basicSettingInfo.officeAddress,
            },
          ],
          conditions: [
            {
              key: "client_no",
              value: this.client_details.client_no,
            },
          ],
        }
      );
      if (data.success) {
        this.getClientData();
        this.$store.dispatch("setClientData", {
          client_name: this.basicSettingInfo.company_name,
          client_no: this.client_details.client_no,
        });
      }
    },
    /**
     * @description function to create clientDetails
     * @name callCreateAPI
     */
    async callCreateAPI() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERCREATEMCLIENTBASEINFO,
        [
          {
            client_no: this.client_details.client_no,
            client_account_no: this.client_details.client_no,
            client_mail: this.basicSettingInfo.email,
            client_name: this.basicSettingInfo.company_name,
            client_person_name: this.basicSettingInfo.person_in_charge,
            client_tel: this.basicSettingInfo.phoneNo,
            client_main_office_kbn: this.selectedoffice,
            del_flg: false,
            create_id: this.client_details.client_no,
            create_dt:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            update_id: this.client_details.client_no,
            update_dt:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            created_at:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            updated_at:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            client_main_office_addr: this.basicSettingInfo.officeAddress,
          },
        ]
      );
      if (data.success) {
        this.getClientData();
      }
    },
  },
};
</script>
<style scoped lang="scss">
$link-color: #6790b9;
.textFieldClass {
  background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box;
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #e6e6e6;
  border-radius: 4px !important;
  opacity: 1;
}
.optionClass {
  text-align-last: left !important;
  background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 30px #00000014;
  border-radius: 50px !important;
  opacity: 1;
}
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
  letter-spacing: 0px;
  color: #4b4b4b;
  opacity: 1;
}
.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  background: #033170 0% 0% no-repeat padding-box;
  border-radius: 4px;
  opacity: 1;
  letter-spacing: 0px;
  color: #ffffff;
  opacity: 1;
}
.required-btnAny1 {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  cursor: default !important;
  border-radius: 4px !important;
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #033170;
  border-radius: 4px;
  letter-spacing: 0px;
  color: #033170;
  opacity: 1;
}
.collclass2 {
  border: 1px solid #c8ced3;
  border-radius: 1rem;
}
.collclass3 {
  border: 1px solid #c8ced3;
  border-radius: 0.4rem;
}
.collclass4 {
  border: 1px solid #c8ced3;
}
.label {
  font-weight: 600 !important;
  display: inline-block;
  margin-bottom: 0.01rem;
}

.btncustom {
  margin-right: 2px;
  text-decoration: underline;
}
.btncustom2 {
  margin-right: 2px;
  text-decoration: underline;
}
.red {
  color: $link-color;
  background: #fff !important;
}
.rowpadding {
  margin-right: 0px;
  margin-left: 0px;
}
.marginchange {
  margin-right: 0rem !important;
}
.form-control {
  border-radius: inherit;
  height: 40px;
}
.marginbtn {
  padding-left: 1.5rem !important;
  margin-left: 10px;
}
#options .custom-select {
  text-align-last: right;
  height: 40px !important;
  border-radius: 0px;
  border: 1px solid #a9a6a7;
}
</style>