<template>
  <div class="p-2 pt-4">
    <b-row>
      <b-col
        ><b-button
          @click="inputExpense"
          variant="primary"
          class="ExpenseButton"
          >{{ $t("InputExpensButton") }}</b-button
        ></b-col
      >
    </b-row>
    <br />

    <b-table
      ref="selectableTable"
      :busy="isBusy"
      :fields="fieldss"
      :items="agencyTableData"
      :borderless="false"
      :outlined="false"
      :bordered="false"
      :striped="false"
      small
      fixed
      sticky-header
      responsive="sm"
      head-variant="light"
    >
      <template v-slot:cell(ProcessStatus)="item">
        <template v-if="item.value == '01' || item.value == '02'">
          <b-button
            :class="{
              redBtn: item.value == '01',
              skyBlueBtn: item.value == '02',
              grayBtn: item.value == '03',
            }"
            >{{ item.value == "01" ? $t("UnProcessed") : $t("Prossesing") }}
          </b-button>
        </template>
        <template v-else>
          <b-button :class="{ grayBtn: item.value == '03' }">{{
            $t("AgencyProcessed")
          }}</b-button>
        </template>
      </template>
      <template v-slot:cell(RequestDate)="item">{{ item.value }}</template>
      <template v-slot:cell(PersonIncharge)="item">{{ item.value }}</template>
      <template v-slot:cell(ProcessDate)="item">{{ item.value }}</template>
      <template v-slot:cell(Icon)>
        <b-button variant="primary" class="FileDownLoadbtn">{{
          $t("FileDownLoad")
        }}</b-button>
      </template>
      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
    </b-table>
    <div class="text-center text-danger mb-2 mt-2">{{ errorMessage }}</div>
  </div>
</template>
<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { getDateHour } from "@/shared/utils";
import { endPoint } from "@/shared/endPoint";

export default {
  data() {
    return {
      agencyTableData: [],
      selectMode: "single",
      isBusy: false,
      selected: [],
      errorMessage: "",
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      client_details: "getClientData",
    }),
    fieldss() {
      return [
        {
          key: "ProcessStatus",
          label: this.$t("INPUTAGENCY.ProcessStatus"),
          class: "text-center title-normal-width",
        },
        {
          key: "RequestDate",
          label: this.$t("INPUTAGENCY.RequestDate"),
          class: "text-center normal-width",
        },
        {
          key: "PersonIncharge",
          label: this.$t("INPUTAGENCY.PersonIncharge"),
          class: "text-center normal-width",
        },
        {
          key: "ProcessDate",
          label: this.$t("INPUTAGENCY.ProcessDate"),
          class: "text-center normal-width",
        },
        {
          key: "Icon",
          label: "",
          class: "text-center title-normal-width",
        },
      ];
    },
  },
  mounted() {
    this.getAgencyData();
  },
  created() {},
  methods: {
    /**
     * @description function to call inputExpense page
     * @name inputExpense
     */
    inputExpense() {
      this.$router.push({ name: "input_expense_items" });
    },
    /**
     * @description function to fetch the agencyInformation
     * @name getAgencyData
     */
    async getAgencyData() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADREQUESTINPUTAGENCY,
        {
          client_no: this.client_details.client_no,
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length > 0) {
          let RequestData = [];
          responseData.forEach((item) => {
            let tableInfo = {
              RequestDate: getDateHour(new Date(item.request_input_agency_dt)),
              ProcessStatus: item.work_sts_kbn,
              ProcessDate: getDateHour(new Date(item.work_dt)),
              PersonIncharge: item.person_name,
            };
            RequestData.push(tableInfo);
          });
          this.agencyTableData = RequestData;
          this.isBusy = false;
        } else {
          this.isBusy = false;
          this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
        }
      }
    },
  },
};
</script>
<style   lang="scss">
$primary: #062854 !default;
$unread_color: #afabab;
$icon_background: red;
.ExpenseButton {
  margin-left: 10px;
}
.redBtn {
  background: red !important;
}
.skyBlueBtn {
  background: rgb(52, 92, 202) !important;
}
.grayBtn {
  background: gray !important;
}
</style>
<style scoped  lang="scss">
.ExpenseButton {
  margin-left: 10px;
}
.redBtn {
  background: red !important;
}
.skyBlueBtn {
  background: rgb(52, 92, 202) !important;
}
.grayBtn {
  background: gray !important;
}
.FileDownLoadbtn {
  font-size: revert;
}
.title-normal-width {
  width: 105px !important;
}
.b-table-sticky-header {
  border: 1px solid #c8ced3;
  margin-bottom: 0px;
}
</style>
