<template>
  <div>
    <label class="highLightHeaderLabel">{{ client_details.client_name }}</label>
    <div>
      <div v-if="clientTab" class="row rowpadding">
        <div class="px-2 mt-3 collclass2">
          <div>
            <button
              v-if="getContentLevelAccess('client-process-account-info-crm-16')"
              :class="{ red: defaultClicked }"
              :disabled="
                !getContentLevelAccess('client-process-account-info-crm-16')
              "
              @click="toggleState"
              class="btn btn-primary btncustom"
            >
              {{ $t("AcoountInformation") }}
            </button>
            <button
              :class="{ red: secondButtonClicked }"
              @click="basicSettings"
              class="btn btn-primary btncustom"
            >
              {{ $t("BasicInformation") }}
            </button>
            <button
              :class="{ red: thirdButtonClicked }"
              @click="companysizeDetails"
              class="btn btn-primary btncustom"
            >
              {{ $t("CompanyInformation") }}
            </button>
            <button
              v-if="getContentLevelAccess('client-process-others-crm-66')"
              :class="{ red: fourthButtonClicked }"
              :disabled="!getContentLevelAccess('client-process-others-crm-66')"
              @click="otherDetails"
              class="btn btn-primary btncustom"
            >
              {{ $t("Others") }}
            </button>
            <button
              :class="{ red: fifthButtonClicked }"
              @click="expenseDetails"
              class="btn btn-primary btncustom"
            >
              {{ $t("ExpenseReduction") }}
            </button>
            <button
              v-if="
                getContentLevelAccess('client-process-excellence-point-crm-91')
              "
              :class="{ red: sixthButtonClicked }"
              :disabled="
                !getContentLevelAccess('client-process-excellence-point-crm-91')
              "
              @click="excellentPointButton"
              class="btn btn-primary btncustom"
            >
              {{ $t("XCELLENTPOINT") }}
            </button>
          </div>
        </div>
        <div class="collclass3">
          <i @click="showAgencyBtn" class="fa fa-caret-right fa-5x" />
        </div>
      </div>
      <div v-if="agencyTab" class="row rowpadding">
        <div class="px-2 mt-3 collclass2">
          <div class="collclass">
            <button
              :class="{ red: seventhButtonClicked }"
              @click="expenseReduction"
              class="btn btn-primary btncustom2"
            >
              {{ $t("ExpenseItemReduction") }}
            </button>
            <button
              v-if="
                getContentLevelAccess(
                  'client-process-subsidy-assessment-crm-92'
                )
              "
              :class="{ red: eigthButtonClicked }"
              :disabled="
                !getContentLevelAccess(
                  'client-process-subsidy-assessment-crm-92'
                )
              "
              @click="subsidyAssessment"
              class="btn btn-primary btncustom2"
            >
              {{ $t("Subsidyassessment") }}
            </button>
            <button
              v-if="getContentLevelAccess('client-process-input-agency-crm-73')"
              :class="{ red: ninethButtonClicked }"
              :disabled="
                !getContentLevelAccess('client-process-input-agency-crm-73')
              "
              @click="inputAgency"
              class="btn btn-primary btncustom2 phase2Btn"
            >
              {{ $t("SERVICEAGENCY") }}
            </button>
            <button
              :class="{ red: tenthButtonClicked }"
              @click="contractInfo"
              class="btn btn-primary btncustom2"
            >
              {{ $t("CONTRACTINFO") }}
            </button>
            <button
              :class="{ red: eleventhButtonClicked }"
              @click="clientManagement"
              class="btn btn-primary btncustom2"
            >
              {{ $t("CUSTOMERSERVICE") }}
            </button>
          </div>
        </div>
        <div class="collclass3">
          <i @click="showClientBtn" class="fa fa-caret-left fa-5x" />
        </div>
      </div>
    </div>
    <div>
      <div v-if="tabIndex == 0" class="collclass4 p-2 pt-4">
        <b-row>
          <b-col cols="12" xl="2" class="mx-md-3 marginAlign">
            <label class="highLightHeaderLabel">{{ $t("LOGINID") }}</label>
          </b-col>
          <b-col cols="12" xl="3" class="px-4">
            <b-form-input
              v-model.trim="loginId"
              class="form-control"
              disabled
            ></b-form-input>
          </b-col>
        </b-row>
        <br />
        <b-row>
          <b-col cols="12" xl="2" class="mx-md-3 marginAlign">
            <label class="highLightHeaderLabel">{{
              $t("LOGINPASSWORD2")
            }}</label>
          </b-col>
          <b-col cols="12" xl="3" class="px-4">
            <b-form-input
              id="input-pwd"
              v-model.trim="loginPass"
              type="password"
              class="form-control"
              disabled
            ></b-form-input>
          </b-col>
          <b-col cols="12" xl="6" class="px-3 mt-1 marginAlign">
            <b-button
              :disabled="
                !getContentLevelAccess('password-reset-completed-crm-63')
              "
              @click="resetPassword"
              variant="primary"
              >{{ $t("RESETPASSWORDBUTTON") }}
              <i v-if="isBusy" class="fa fa-spinner fa-spin" />
            </b-button>
            <p class="customText label">{{ $t("RESETBUTTONTEXT") }}</p>
          </b-col>
        </b-row>
        <b-row class="mt-4">
          <b-col cols="12" xl="2" class="mx-md-3 marginAlign">
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("LOGINTIME") }}</label>
          </b-col>
          <b-col cols="12" xl="3" class="px-4">
            <b-form-input
              v-model.trim="loginTime"
              class="form-control"
              disabled
            ></b-form-input>
          </b-col>
        </b-row>
        <br />
        <b-row>
          <b-col cols="12" xl="2" class="mx-md-3 marginAlign">
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{ $t("LOGINTIME2") }}</label>
          </b-col>
          <b-col cols="12" xl="3" class="px-4">
            <b-form-input
              v-model.trim="loginTime2"
              class="form-control"
              disabled
            ></b-form-input>
          </b-col>
        </b-row>
        <br />
      </div>
      <div v-if="tabIndex == 1" class="collclass4">
        <SettingTabOne />
      </div>
      <div v-if="tabIndex == 2" class="collclass4">
        <SettingTabTwo />
      </div>
      <div v-if="tabIndex == 3" class="collclass4 p-2">
        <SettingTabThree> </SettingTabThree>
      </div>
      <div v-if="tabIndex == 4" class="collclass4">
        <ExpenseInfo> </ExpenseInfo>
      </div>
      <div v-if="tabIndex == 5" class="collclass4">
        <ClientExcelentPoint />
      </div>
      <div v-if="tabIndex == 6" class="collclass4">
        <div>
          <ExpenseReduction></ExpenseReduction>
        </div>
      </div>
      <div v-if="tabIndex == 7" class="collclass4">
        <Subsidyassessment> </Subsidyassessment>
      </div>
      <div v-if="tabIndex == 8" class="collclass4">
        <InputAgency> </InputAgency>
      </div>
      <div v-if="tabIndex == 9" class="collclass4">
        <ContractInformation> </ContractInformation>
      </div>
      <div v-if="tabIndex == 10" class="collclass4">
        <ClientManagement />
      </div>
      <b-modal
        id="Client-modal1"
        ref="Client-modal1"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="lg"
        footer-class="border-0"
        header-class="border-0 bg-white pd-0"
        button-size="sm"
      >
        <template>
          <div class="float-left">
            <b-button
              @click="goBack"
              class="back-btn close-btn mt-2"
              variant="primary"
              >{{ $t("BACK") }}</b-button
            >
          </div>
          <br />
          <div>
            <br />
            <br />
            <label class="px-4 txt-align highLightHeaderLabel"
              >{{ $t("MailMessage1") }}{{ email
              }}{{ $t("MailMessage2") }}</label
            >
          </div>
          <br />
          <div class="px-4 highLightHeaderLabel txt-align">
            <label>{{ $t("ClientName") }} : {{ companyName }}</label
            ><br />
            <label>{{ $t("PersonIncharge") }} : {{ personIncharge }}</label>
          </div>
        </template>
      </b-modal>
    </div>
    <br />
  </div>
</template>
<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { mapGetters } from "vuex";
import SettingTabOne from "@/components/settings/SettingTabOne";
import SettingTabTwo from "@/components/settings/SettingTabTwo";
import SettingTabThree from "@/components/settings/SettingTabThree";
import ExpenseInfo from "@/components/settings/InputExpense";
import ExpenseReduction from "@/components/settings/ExpenseReduction";
import InputAgency from "@/components/settings/InputAgency";
import ContractInformation from "@/components/settings/ContractInformation";
import ClientManagement from "@/components/settings/ClientManagement";

import Subsidyassessment from "@/views/management/clientManagement/SubsidyAssesMent";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
import ClientExcelentPoint from "@/components/management/ClientExcelentPoint";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";

export default {
  name: "Index",
  components: {
    SettingTabOne,
    SettingTabTwo,
    SettingTabThree,
    ExpenseInfo,
    ExpenseReduction,
    InputAgency,
    ContractInformation,
    ClientManagement,
    Subsidyassessment,
    ClientExcelentPoint,
  },
  data() {
    return {
      isBusy: false,
      email: "",
      tabIndex: 0,
      loginId: "",
      loginPass: "********",
      loginTime: "",
      loginTime2: "",
      clientTab: true,
      agencyTab: false,
      companyTitle: "",
      companyName: "",
      personIncharge: "",
      isActive: true,
      defaultClicked: false,
      secondButtonClicked: false,
      thirdButtonClicked: false,
      fourthButtonClicked: false,
      fifthButtonClicked: false,
      sixthButtonClicked: false,
      seventhButtonClicked: false,
      eigthButtonClicked: false,
      ninethButtonClicked: false,
      tenthButtonClicked: false,
      eleventhButtonClicked: false,
      activeTab: 0,
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      client_details: "getClientData",
    }),
  },
  fieldss() {},
  validations: {},
  mounted() {
    if (!this.client_details.client_no) {
      this.$router.push({ name: "client_list" });
    } else {
      this.activeTab = localStorage.getItem("activeTab");
      this.tabSwitch();
      this.defaultButtonColor();
      this.getLoginDetails();
      this.getClientData();
    }
  },
  created() {},
  methods: {
    /**
     * @description function to check the authorization
     * @name getContetLevelAccess
     */
    getContentLevelAccess(accessID) {
      let checkData = JSON.parse(localStorage.getItem("dummyData"));
      const foundAccess = checkData ? checkData.hasOwnProperty(accessID) : true;
      if (foundAccess) {
        return false;
      } else {
        return true;
      }
    },
    /**
     * @description function to show the active tab
     * @name tabSwitch
     */
    tabSwitch() {
      switch (this.activeTab) {
        case "0":
          return this.toggleState();
        case "1":
          return this.basicSettings();
        case "2":
          return this.companysizeDetails();
        case "3":
          return this.otherDetails();
        case "4":
          return this.expenseDetails();
        case "5":
          return this.excellentPointButton();
        case "6":
          return this.expenseReduction();
        case "7":
          return this.subsidyAssessment();
        case "8":
          return this.inputAgency();
        case "9":
          return this.contractInfo();
        case "10":
          return this.clientManagement();

        default:
          break;
      }
    },
    /**
     * @description function to get all tab indexes
     * @name getTabIndex
     */
    getTabIndex() {
      if (
        this.client_details.tab_index > 5 &&
        this.client_details.tab_index == 7
      ) {
        this.showAgencyBtn();
        this.subsidyAssessment();
      }
      if (
        this.client_details.tab_index > 5 &&
        this.client_details.tab_index == 6
      ) {
        this.showAgencyBtn();
        this.expenseReduction();
      }
      if (
        this.client_details.tab_index > 5 &&
        this.client_details.tab_index == 9
      ) {
        this.showAgencyBtn();
        this.contractInfo();
      }
    },
    /**
     * @description function to dispaly the tab based on the tabIndex
     * @name defaultButtonColor
     */
    defaultButtonColor() {
      if (this.tabIndex == 0) {
        this.defaultClicked = true;
      } else {
        this.defaultClicked = false;
      }
      if (
        this.tabIndex != 1 ||
        this.tabIndex != 2 ||
        this.tabIndex != 3 ||
        this.tabIndex != 4 ||
        this.tabIndex != 5 ||
        this.tabIndex != 6 ||
        this.tabIndex != 7 ||
        this.tabIndex != 8 ||
        this.tabIndex != 9 ||
        this.tabIndex != 10
      ) {
        this.secondButtonClicked = false;
        this.thirdButtonClicked = false;
        this.fourthButtonClicked = false;
        this.fifthButtonClicked = false;
        this.sixthButtonClicked = false;
        this.seventhButtonClicked = false;
        this.eigthButtonClicked = false;
        this.ninethButtonClicked = false;
        this.tenthButtonClicked = false;
        this.eleventhButtonClicked = false;
      }
    },
    /**
     * @description function to Fetch the accountDetails
     * @name getLogInDetails
     */
    async getLoginDetails() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMCLIENTBASEINFO,
        {
          fieldlist: ["client_account_no"],
          conditions: [
            {
              key: "client_no",
              value: this.client_details.client_no,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        let client_account_no;
        if (responseData.length > 0) {
          client_account_no = responseData[0].client_account_no;
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERREADDATAMCLIENTACCOUNT,
            {
              fieldlist: [
                "client_account_no",
                "client_login_id",
                "first_login_dt",
                "last_login_dt",
              ],
              conditions: [
                {
                  key: "client_account_no",
                  value: client_account_no,
                },
              ],
            }
          );
          if (data.success) {
            let responseData = data.data;
            if (responseData.length > 0) {
              let login = new Date(responseData[0].first_login_dt);
              let lastLogin = new Date(responseData[0].last_login_dt);
              this.loginId = responseData[0].client_login_id;
              this.loginTime = login
                ? login.getFullYear() +
                  "-" +
                  this.getMonth(login.getUTCMonth() + 1) +
                  "-" +
                  this.getDate(login.getUTCDate()) +
                  " " +
                  this.getMonth(login.getHours()) +
                  ":" +
                  this.getMonth(login.getMinutes())
                : "";
              this.loginTime2 = lastLogin
                ? lastLogin.getFullYear() +
                  "-" +
                  this.getMonth(lastLogin.getUTCMonth() + 1) +
                  "-" +
                  this.getDate(lastLogin.getUTCDate()) +
                  " " +
                  this.getMonth(lastLogin.getHours()) +
                  ":" +
                  this.getMonth(lastLogin.getMinutes())
                : "";
            }
          }
        }
      }
    },
    /**
     * @description function to add 0 before the month if month less than 10
     * @name getMonth
     */
    getMonth(month) {
      if (month > 0 && month < 10) {
        return "0" + month;
      }
      return month;
    },
    /**
     * @description function to add 0  before the date if it is less than 10
     * @name getDate
     */
    getDate(date) {
      if (date > 0 && date < 10) {
        return "0" + date;
      }
      return date;
    },
    /**
     * @description function to fetch clientDetails
     * @name getClientData
     */
    async getClientData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMCLIENTBASEINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: this.client_details.client_no,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length) {
          this.email = responseData[0].client_mail;
          this.companyName = responseData[0].client_name;
          this.personIncharge = responseData[0].client_person_name;
        }
      }
    },
    /**
     * @description function to set tabIndex for basic setting screen
     * @name basicSettings
     */
    basicSettings() {
      this.tabIndex = 1;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.secondButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for companysizeDetails screen
     * @name companysizeDetails
     */
    companysizeDetails() {
      this.tabIndex = 2;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.thirdButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for otherDetails screen
     * @name otherDetails
     */
    otherDetails() {
      this.tabIndex = 3;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.fourthButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for expenseDetails screen
     * @name expenseDetails
     */
    expenseDetails() {
      this.tabIndex = 4;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.fifthButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for excellentPointButton screen
     * @name excellentPointButton
     */
    excellentPointButton() {
      this.tabIndex = 5;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.sixthButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for expenseReduction screen
     * @name expenseReduction
     */
    expenseReduction() {
      this.showAgencyBtn();
      this.tabIndex = 6;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.seventhButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for inputAgency screen
     * @name inputAgency
     */
    inputAgency() {
      //phase2
      // this.showAgencyBtn();
      // this.tabIndex = 8;
      // localStorage.setItem("activeTab", this.tabIndex);
      // this.defaultButtonColor();
      // setTimeout(() => {
      //   this.ninethButtonClicked = true;
      // }, 0);
    },
    /**
     * @description function to set tabIndex for clientManagement screen
     * @name clientManagement
     */
    clientManagement() {
      this.showAgencyBtn();
      this.tabIndex = 10;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.eleventhButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for contractInfo screen
     * @name contractInfo
     */
    contractInfo() {
      this.showAgencyBtn();
      this.tabIndex = 9;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
      setTimeout(() => {
        this.tenthButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to show the client tabs
     * @name showClientBtn
     */
    showClientBtn() {
      this.clientTab = true;
      this.agencyTab = false;
    },
    /**
     * @description function to show the Agency tabs
     * @name showAgencyBtn
     */
    showAgencyBtn() {
      this.clientTab = false;
      this.agencyTab = true;
    },
    /**
     * @description function to set tabIndex for subsidyAssessment screen
     * @name subsidyAssessment
     */
    subsidyAssessment() {
      this.showAgencyBtn();
      this.tabIndex = 7;
      this.defaultButtonColor();
      localStorage.setItem("activeTab", this.tabIndex);
      setTimeout(() => {
        this.eigthButtonClicked = true;
      }, 0);
    },
    /**
     * @description function to set tabIndex for toggleState screen
     * @name toggleState
     */
    toggleState() {
      this.tabIndex = 0;
      localStorage.setItem("activeTab", this.tabIndex);
      this.defaultButtonColor();
    },
    /**
     * @description function to reset the password
     * @name resetPassword
     */
    async resetPassword() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(endPoint.FORGOTPASSWORD, {
        remind_mail: this.email,
      });
      if (data.success) {
        this.isBusy = false;
        this.$refs["Client-modal1"].show();
      }
    },
    /**
     * @description function to hide the modl
     * @name goBack
     */
    goBack() {
      this.$refs["Client-modal1"].hide();
    },
    /**
     * @description function to make pop-up model using html
     * @name getSuccessHtml
     */
    getSuccessHtml() {
      let successHtml =
        `<div class="font-weight-bold text-left">` +
        this.$t("MailMessage1") +
        `<span class="text-blue">` +
        this.email +
        `</span>` +
        this.$t("MailMessage2") +
        `</div></div> <br/>`;
      successHtml +=
        `<div class="font-weight-bold text-left">` +
        this.$t("ClientName") +
        `<span>` +
        this.user_details.clientCompanyName +
        `</span></div>`;
      successHtml +=
        `<div class="mb-4 font-weight-bold text-left">` +
        this.$t("PersonIncharge") +
        `<span>` +
        this.user_details.clientPersonName +
        `</span>  </div>`;
      return successHtml;
    },
  },
};
</script>
<style scoped lang="scss">
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: initial;
  margin-bottom: 0rem;
}

.collclass2 {
  border: 0px solid #c8ced3;
  border-radius: 1rem;
}
.collclass3 {
  border: 0px solid #c8ced3;
  border-radius: 0.4rem;
}
.collclass4 {
  border: 1px solid #c8ced3;
}
.label {
  font-weight: 600 !important;
  display: inline-block;
  margin-bottom: 0.01rem;
}

.btncustom {
  margin-right: 2px;
  margin-bottom: 2px;
  text-decoration: underline;
}

.btncustom2 {
  margin-right: 2px;
  margin-bottom: 2px;
  text-decoration: underline;
}
.red {
  color: #6790b9 !important;
  background: #fff !important;
}
.rowpadding {
  margin-right: 0px;
  margin-left: 0px;
}

.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  margin-left: 10px;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.form-control {
  border-radius: inherit;
  height: 40px;
}
.customText {
  margin-top: revert;
  font-size: smaller;
}
.txt-align {
  padding-left: 5rem !important;
}
.marginAlign {
  margin-left: 8px;
}
.container-box[data-v-c16e5f8a] {
  box-shadow: 0 0 10px #dbd6d6;
  border-radius: 5px;
  padding: 0.5rem;
  margin-bottom: 1rem;
}
</style>