<template>
  <div class="p-2 pt-4">
    <b-row>
      <b-col cols="12">
        <b-row>
          <b-col cols="3">
            <div class="pointer d-flex align-items-center">
              <b-link
                @click="onPreviousClick"
                class="d-flex align-items-center pointer"
              >
                <span class="px-2">
                  <i
                    class="fa fa-caret-left fa-2x text-muted"
                    aria-hidden="true"
                  ></i>
                </span>
                <span class="font-weight-bold"
                  >{{ $t("INPUTEXPENSEITEM.PREVIOUSMONTH") }} &nbsp;&nbsp;</span
                >
              </b-link>
            </div>
          </b-col>

          <b-col cols="6">
            <div class="pointer align-items-center text-center">
              <span class="font-weight-bold pr-1"
                >{{ financialYear + "" + month + "" }}:
              </span>
              <label v-if="comopanyPosition" v-b-modal.modal-tall>
                <u>{{
                  $t("DASHBOARD.OVERALL_JUDGEMENTS")[comopanyPosition - 1].LABEL
                }}</u></label
              >
            </div>
          </b-col>

          <b-col cols="3">
            <div class="pointer d-flex align-items-center float-right">
              <b-link
                @click="onNextClick"
                class="d-flex align-items-center pointer"
              >
                <span class="font-weight-bold"
                  >{{ $t("INPUTEXPENSEITEM.NEXTMONTH") }} &nbsp;&nbsp;</span
                >

                <span @click="onNextClick" class="px-2">
                  <i
                    class="fa fa-caret-right fa-2x text-muted"
                    aria-hidden="true"
                  ></i>
                </span>
              </b-link>
            </div>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <b-table
      ref="selectableTable"
      :select-mode="selectModeTable"
      :fields="fields"
      :busy="isBusy"
      :items="inputExpenseTableData"
      :borderless="false"
      :outlined="false"
      :bordered="false"
      :striped="false"
      small
      fixed
      sticky-header
      responsive="sm"
      head-variant="light"
    >
      <template v-slot:cell(account)="item">{{ item.value.value }}</template>
      <template v-slot:cell(expenseItem)="item">{{
        item.value.value
      }}</template>
      <template v-slot:cell(expense)="item">{{ item.value.value }}</template>
      <template v-slot:cell(expansionItem1)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem2)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem3)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div></template
      >
      <template v-slot:cell(expansionItem4)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem5)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(expansionItem6)="item">
        <div>{{ item.value.name }}</div>
        <div>{{ item.value.value }}</div>
      </template>
      <template v-slot:cell(CreateDate)="item">
        {{ item.value.value }}
      </template>
      <template v-slot:cell(JudgementDate)="item">
        {{ item.value.value }}
      </template>
      <template v-slot:cell(expansionItem9)="item">
        <template>
          <b-button
            :class="{
              skyBlueBtn: item.value == '1',
              greenBtn: item.value == '2',
              orangeBtn: item.value == '3',
              roseBtn: item.value == '4',
              grayBtn: item.value == '5',
              grayBtn: item.value == '0',
            }"
            class="float-left clrBtn"
            >{{ judgIcon[item.value] }}
          </b-button>
        </template>
      </template>
      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
    </b-table>
    <div class="text-center text-danger mb-2 mt-2">{{ errorMessage }}</div>
    <b-modal
      id="modal-tall"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white"
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="w-100 d-flex justify-content-end">
          <button
            @click="cancel()"
            class="cross-close-btn border-dark text-dark font-weight-bold text-sm m-0 mr-2 bg-white"
          >
            X
          </button>
        </div>
      </template>
      <b-card class="pop-up-box">
        <div class="my-0 py-0 px-2">
          <ul class="list-unstyled my-0 py-0">
            <li
              v-for="(OVERALL_JUDGEMENT, index) in $t(
                'DASHBOARD.OVERALL_JUDGEMENTS'
              )"
              :key="index"
            >
              <span class="font-weight-bold block">
                {{ OVERALL_JUDGEMENT.LABEL }}
              </span>
              <p class="mb-0">{{ OVERALL_JUDGEMENT.VALUE }}</p>
            </li>
          </ul>
        </div>
      </b-card>
      <template v-slot:modal-footer="{ cancel }">
        <div class="w-100">
          <button
            @click="cancel()"
            class="btn-sm float-left btn-primary px-1 ml-2"
          >
            {{ $t("CLOSE") }}
          </button>
        </div>
      </template>
    </b-modal>
  </div>
</template>
<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { getDateHour } from "@/shared/utils";
import { endPoint } from "@/shared/endPoint";

export default {
  data() {
    return {
      inputExpenseTableData: [],
      isBusy: false,
      financialYear: "",
      year: "",
      month: "",
      comopanyPosition: "",
      selectModeTable: "single",
      judgIcon: {
        1: this.$t("GRAPH.LEGENDS[0].LEGEND"),
        2: this.$t("GRAPH.LEGENDS[1].LEGEND"),
        3: this.$t("GRAPH.LEGENDS[2].LEGEND"),
        4: this.$t("GRAPH.LEGENDS[3].LEGEND"),
        5: this.$t("GRAPH.LEGENDS[4].LEGEND"),
        0: this.$t("JUDGEICONNULL"),
      },
      info: {},
      errorMessage: "",
    };
  },
  validation: {},
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      client_details: "getClientData",
    }),
    fields() {
      return [
        {
          key: "account",
          label: this.$t("INPUTEXPENSE.account"),
          class: "text-center normal-width",
        },
        {
          key: "expenseItem",
          label: this.$t("INPUTEXPENSE.expenseItem"),
          class: "text-center normal-width",
        },
        {
          key: "expense",
          label: this.$t("INPUTEXPENSE.expense"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem1",
          label: this.$t("INPUTEXPENSE.expansionItem1"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem2",
          label: this.$t("INPUTEXPENSE.expansionItem2"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem3",
          label: this.$t("INPUTEXPENSE.expansionItem3"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem4",
          label: this.$t("INPUTEXPENSE.expansionItem4"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem5",
          label: this.$t("INPUTEXPENSE.expansionItem5"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem6",
          label: this.$t("INPUTEXPENSE.expansionItem6"),
          class: "text-center normal-width",
        },
        {
          key: "CreateDate",
          label: this.$t("INPUTEXPENSE.CreateDate"),
          class: "text-center normal-width",
        },
        {
          key: "JudgementDate",
          label: this.$t("INPUTEXPENSE.JudgementDate"),
          class: "text-center normal-width",
        },
        {
          key: "expansionItem9",
          label: this.$t("INPUTEXPENSE.Icon"),
          class: "text-center title-normal-width",
        },
      ];
    },
  },
  mounted() {
    this.getExpansionItemList();
  },
  methods: {
    /**
     * @description function to set Setting Previous Month And  Previous Year And Setting List Details
     * @name onPreviousClick
     */
    onPreviousClick() {
      this.month = parseInt(this.month) - 1;
      if (this.yearStartMonth > parseInt(this.month)) {
        this.financialYear = parseInt(this.year) - 1;
        if (this.month <= 0) {
          this.year = parseInt(this.year) - 1;
          this.month = 12;
        } else if (this.month > 9) {
          this.month = this.month;
        } else {
          this.month = "0" + this.month;
        }
      } else {
        if (this.month <= 0) {
          this.year = parseInt(this.year) - 1;
          this.month = 12;
        } else if (this.month > 9) {
          this.month = this.month;
        } else {
          this.month = "0" + this.month;
        }
      }
      this.getExpansionItemList();
      this.getcomopanyPosition();
    },
    /**
     * @description function to set Setting next Month And  next Year And Setting List Details
     * @name onNextClick
     */
    onNextClick() {
      this.month = parseInt(this.month) + 1;
      if (this.month > 12) {
        this.year = parseInt(this.year) + 1;
        this.month = "01";
      } else if (this.month > 9) {
        this.month = this.month;
      } else {
        this.month = "0" + this.month;
      }

      if (this.yearStartMonth > parseInt(this.month)) {
        this.financialYear = parseInt(this.year) - 1;
      } else {
        this.financialYear = parseInt(this.year);
      }
      this.getExpansionItemList();
      this.getcomopanyPosition();
    },
    /**
     * @description function to Fetch Year And Month
     * @name getInputAgencyFlag
     */
    async getInputAgencyFlag() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["client_no", "input_agency_option", "year_start_month"],
          conditions: [
            {
              key: "client_no",
              value: this.client_details.client_no,
            },
          ],
        }
      );
      let responseData = data.data;
      if (responseData.length > 0) {
        this.agencyFlag = responseData[0].input_agency_option;
        this.yearStartMonth = responseData[0].year_start_month;
        if (this.yearStartMonth > parseInt(this.month)) {
          this.financialYear = parseInt(this.year) - 1;
        } else {
          this.financialYear = parseInt(this.year);
        }
        this.getcomopanyPosition();
      }
    },
    /**
     * @description function to Fetch List Details Of EXpense Items
     * @name getExpenseItemList
     */
    async getExpansionItemList() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONCLIENTCOSTEXPANSIONITEMLIST,
        {
          client_no: this.client_details.client_no,
          year: this.year,
          month: this.month,
        }
      );
      if (data.success) {
        let responseData = data.data.clientCostExpansionInfoListObject;
        if (this.month == "" && this.year == "") {
          this.year = data.data.year;
          this.month = data.data.month;
          this.getInputAgencyFlag();
        }
        if (responseData.length > 0) {
          let rowData = [];
          let disabledItems = [];
          responseData.map((item) => {
            disabledItems.push(item.expense_item_nm);
            let ExpenseItems = [];
            for (let i in this.$t("ARRAYDATA.Array")) {
              let filterCondition =
                item.expansion_items[i] &&
                item.expansion_items[i].expansion_item_format_kbn != null
                  ? item.expansion_items[i].expansion_item_format_kbn
                  : "";
              ExpenseItems.push({
                name:
                  item.expansion_items[i] &&
                  item.expansion_items[i].expansion_item_nm != null
                    ? item.expansion_items[i].expansion_item_nm
                    : "-",
                value:
                  item.expansion_items[i] &&
                  item.expansion_items[i].expansion_item_value != null
                    ? filterCondition == "02"
                      ? this.filterByCurrency(
                          item.expansion_items[i].expansion_item_value
                        )
                      : item.expansion_items[i].expansion_item_value
                    : "-",
                id:
                  item.expansion_items[i] &&
                  item.expansion_items[i].cost_expansion_item_no != null
                    ? item.expansion_items[i].cost_expansion_item_no
                    : "-",
                kbn:
                  item.expansion_items[i] &&
                  item.expansion_items[i].expansion_item_format_kbn != null
                    ? item.expansion_items[i].expansion_item_format_kbn
                    : "-",
              });
            }
            let data = {
              expansionItem1: ExpenseItems[0],
              expansionItem2: ExpenseItems[1],
              expansionItem3: ExpenseItems[2],
              expansionItem4: ExpenseItems[3],
              expansionItem5: ExpenseItems[4],
              expansionItem6: ExpenseItems[5],
              account: {
                name: this.$t("INPUTEXPENSE.account"),
                value: item.accounts_name_nm,
                code: item.accounts_name_cd,
              },
              expenseItem: {
                name: this.$t("INPUTEXPENSE.expenseItem"),
                value: item.expense_item_nm,
                code: item.expense_item_cd,
              },
              expense: {
                name: this.$t("INPUTEXPENSE.expense"),
                value: this.filterByCurrency(item.cost),
              },
              CreateDate: {
                value: item.expansion_items[0].cost_info_create_dt
                  ? getDateHour(
                      new Date(item.expansion_items[0].cost_info_create_dt)
                    )
                  : "",
              },
              JudgementDate: {
                value: item.expansion_items[0].judg_dt
                  ? getDateHour(new Date(item.expansion_items[0].judg_dt))
                  : "",
              },

              expansionItem9: item.expansion_items[0].judg
                ? item.expansion_items[0].judg
                : "0",
            };
            rowData.push(data);
          });
          this.isBusy = false;
          this.inputExpenseTableData = rowData;
          this.relatedFlagInfo = false;
        } else {
          this.isBusy = false;
          this.inputExpenseTableData = [];
          this.relatedFlagInfo = false;
        }
      } else {
        this.isBusy = false;
        this.inputExpenseTableData = [];
        this.relatedFlagInfo = false;
        this.year = new Date().getFullYear();
        let currentMonth = new Date().getMonth();
        if (currentMonth < 9) {
          this.month = "0" + (parseInt(currentMonth) + 1);
        } else {
          this.month = parseInt(currentMonth) + 1;
        }
        this.financialYear = this.year;
        this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
      }
    },
    /**
     * @description function to Fetch the Postion of The company
     * @name getcompanyPosition
     */
    async getcomopanyPosition() {
      let yearMonth = this.financialYear.toString() + this.month;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATATCLIENTJUDGINFOT,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: this.client_details.client_no,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        responseData.forEach((item) => {
          if (item.judg_monthly == yearMonth) {
            this.info = {
              monthly: item.judg_monthly,
              position: item.judg_company,
            };
          }
        });
        if (this.info.monthly == yearMonth) {
          this.comopanyPosition = this.info.position;
        } else {
          this.comopanyPosition = "";
        }
      }
    },
    /**
     * @description function to convert number to currency
     * @name filterByCurrency
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
  },
};
</script>
<style lang="scss">
.redBtnn {
  padding: 3px 5px 3px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
  background: red !important;
}
.skyBlueBtn {
  background: #01b0f0 !important;
}
.greenBtn {
  background: #a9d18e !important;
}
.orangeBtn {
  background: #ffe699 !important;
}
.roseBtn {
  background: #ff7c80 !important;
}
</style>
<style scoped lang="scss">
.redBtnn {
  padding: 3px 5px 3px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
  background: red !important;
}
.skyBlueBtn {
  background: #01b0f0 !important;
}
.greenBtn {
  background: #a9d18e !important;
}
.orangeBtn {
  background: #ffe699 !important;
}
.roseBtn {
  background: #ff7c80 !important;
}
.clrBtn {
  min-width: 100px !important;
}
.marginAlign {
  margin-left: -100px;
}
</style>