<template>
  <div>
    <b-row class="mt-4 mb-4">
      <b-col
        ><b-button
          @click="createContractInfo"
          variant="primary"
          class="ContractInfo"
          >{{ $t("CONTRACTADDBUTTON") }}</b-button
        ></b-col
      >
    </b-row>

    <b-table
      ref="selectableTable"
      :busy="isBusy"
      :select-mode="selectModeTable"
      :fields="fieldss"
      :items="tableData"
      :borderless="false"
      :outlined="false"
      :bordered="false"
      :striped="false"
      small
      fixed
      sticky-header
      responsive="sm"
      head-variant="light"
    >
      <template v-slot:cell(Supplier)="item">{{ item.value }}</template>
      <template v-slot:cell(Account)="item">{{ item.value }}</template>
      <template v-slot:cell(Expense)="item">{{ item.value }}</template>
      <template v-slot:cell(ApplicationDate)="item">{{ item.value }}</template>
      <template v-slot:cell(ContractStatus)="item">{{ item.value }}</template>
      <template v-slot:cell(ContractStartDate)="item">{{
        item.value
      }}</template>
      <template v-slot:cell(ContractPersonIncharge)="item">{{
        item.value
      }}</template>
      <template v-slot:cell(Icon)="item">
        <b-button @click="editContractInfo(item)" variant="primary">{{
          $t("EDITBUTTON")
        }}</b-button>
      </template>
      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
    </b-table>
    <div class="text-center text-danger mb-2 mt-2">{{ errorMessage }}</div>
  </div>
</template>
<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { mapGetters } from "vuex";
import { getDate } from "@/shared/utils";
import { endPoint } from "@/shared/endPoint";

export default {
  data() {
    return {
      tableData: [],
      selectModeTable: "single",
      selected: [],
      isBusy: false,
      errorMessage: "",
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      client_details: "getClientData",
    }),
    fieldss() {
      return [
        {
          key: "Supplier",
          label: this.$t("CONTRACTINFORMATION.Supplier"),
          class: "text-center normal-width",
        },
        {
          key: "Account",
          label: this.$t("CONTRACTINFORMATION.Account"),
          class: "text-center normal-width",
        },
        {
          key: "Expense",
          label: this.$t("CONTRACTINFORMATION.Expense"),
          class: "text-center normal-width",
        },
        {
          key: "ApplicationDate",
          label: this.$t("CONTRACTINFORMATION.ApplicationDate"),
          class: "text-center normal-width",
        },

        {
          key: "ContractStatus",
          label: this.$t("CONTRACTINFORMATION.ContractStatus"),
          class: "text-center normal-width",
        },
        {
          key: "ContractStartDate",
          label: this.$t("CONTRACTINFORMATION.ContractStartDate"),
          class: "text-center normal-width",
        },
        {
          key: "ContractPersonIncharge",
          label: this.$t("CONTRACTINFORMATION.ContractPersonIncharge"),
          class: "text-center normal-width",
        },
        {
          key: "Icon",
          label: "",
          class: "text-center title-normal-width",
        },
      ];
    },
  },
  mounted() {
    this.contractInfo();
  },
  created() {},
  methods: {
    /**
     * @description function to Call ContractInfoAddEdit Page to create Contract info
     * @name createContractInfo
     */
    createContractInfo() {
      this.$store.dispatch("setAggrementNo", {
        aggrementno: "",
        flagcheck: true,
      });
      this.$router.push({
        name: "client_mgmt_contract_info_add_edit",
        params: { state: true },
      });
    },
    /**
     * @description function to call ContractInfoAddEdit to edit the contract info
     * @name editContractInfo
     */
    editContractInfo(item) {
      this.$store.dispatch("setAggrementNo", {
        aggrementno: item.item.aggrementno,
        flagcheck: false,
      });
      this.$router.push({
        name: "client_mgmt_contract_info_add_edit",
        params: { state: false },
      });
    },
    /**
     * @description function to fetch contractInfo details
     * @name contractInfo
     */
    async contractInfo() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADAGREEMENT,
        {
          client_no: this.client_details.client_no,
        }
      );
      if (data.success) {
        let responseData = data.data;
        let sortData = this._.sortBy(responseData, "update_dt").reverse();
        if (responseData.length > 0) {
          let RequestData = [];
          sortData.forEach((item) => {
            let tableInfo = {
              Supplier: item.supplier_name,
              Account: item.accounts_name_nm,
              Expense: item.expense_item_nm,
              ApplicationDate: item.agreement_request_date
                ? getDate(new Date(item.agreement_request_date))
                : "",
              ContractStatus: item.agreement_status_name,
              ContractStartDate: item.agreement_start_date
                ? getDate(new Date(item.agreement_start_date))
                : "",
              ContractPersonIncharge: item.person_name,
              aggrementno: item.agreement_no,
            };
            RequestData.push(tableInfo);
          });
          this.tableData = RequestData;
          this.isBusy = false;
        } else {
          this.isBusy = false;
          this.errorMessage = this.$t("GENRALDATAERRORMESSAGE");
        }
      } else {
        this.isBusy = false;
      }
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
$unread_color: #afabab;
$icon_background: red;
.ContractInfo {
  font-size: revert;
  margin-left: 10px;
}
.b-table-sticky-header {
  border: 1px solid #c8ced3;
}
.dataalign {
  text-align: center;
}
</style>
